'use client';
import React, { useState, useRef } from 'react';
import { Cliente, DetalleServicio, VentaServicio } from '../../../entities/venta';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency } from '../../../lib/logistica/formatters';

const clientes: Cliente[] = [
  { idCliente: 'CL-001', nombre: 'ABC Soluciones', telefono: '987654321', correo: 'abc@empresa.com' },
  { idCliente: 'CL-002', nombre: 'XYZ Market', telefono: '999888777', correo: 'xyz@empresa.com' },
];

const servicios = [
  { idServicio: 'SVC-001', nombre: 'Instalación de red', costo: 200 },
  { idServicio: 'SVC-002', nombre: 'Soporte técnico', costo: 120 },
  { idServicio: 'SVC-003', nombre: 'Cableado estructurado', costo: 300 },
];

const VentaRegistroModal = ({ open, onClose }: { open: boolean; onClose: () => void }) => {
  const { registrarVenta } = useVentas();
  const idRef = useRef(0);
  const [cliente, setCliente] = useState<Cliente | null>(null);
  const [detalles, setDetalles] = useState<DetalleServicio[]>([]);
  const [descuento, setDescuento] = useState(0);
  const [error, setError] = useState('');

  const agregarServicio = (id: string) => {
    const servicio = servicios.find(s => s.idServicio === id);
    if (!servicio || detalles.some(d => d.idServicio === id)) return;
    idRef.current++;
    setDetalles(prev => [
      ...prev,
      {
        idDetalleServicio: `DET-${idRef.current}`,
        idServicio: servicio.idServicio,
        nombreServicio: servicio.nombre,
        costoTotal: servicio.costo,
      },
    ]);
  };

  const total = Math.max(0, detalles.reduce((acc, d) => acc + d.costoTotal, 0) - descuento);

  const handleSubmit = () => {
    if (!cliente || detalles.length === 0) {
      setError('⚠️ Selecciona un cliente y al menos un servicio.');
      return;
    }

    const venta: VentaServicio = {
      idVentaServicio: `VENTA-${Date.now()}`,
      cliente,
      detalles,
      descuento,
      costoTotal: total,
      fechaRegistro: new Date().toISOString(),
    };

    registrarVenta(venta);
    onClose();
  };

  if (!open) return null;

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <button className="modal-close-btn" onClick={onClose}>✖</button>
        <h2 className="text-xl font-bold text-indigo-700 mb-4">Registrar nueva venta</h2>

        <select
          className="border p-2 rounded w-full mb-4"
          value={cliente?.idCliente || ''}
          onChange={(e) => {
            const selected = clientes.find(c => c.idCliente === e.target.value);
            if (selected) setCliente(selected);
          }}
        >
          <option value="">Seleccionar cliente</option>
          {clientes.map(c => (
            <option key={c.idCliente} value={c.idCliente}>
              {c.nombre}
            </option>
          ))}
        </select>

        <div className="grid grid-cols-2 gap-3 mb-4">
          {servicios.map(s => (
            <button
              key={s.idServicio}
              onClick={() => agregarServicio(s.idServicio)}
              className="border rounded-lg p-3 bg-white hover:bg-indigo-50 text-left transition"
            >
              <p className="font-medium text-indigo-800">{s.nombre}</p>
              <p className="text-sm text-gray-500">{formatCurrency(s.costo)}</p>
            </button>
          ))}
        </div>

        <ul className="mb-4">
          {detalles.map((d) => (
            <li key={d.idDetalleServicio} className="flex justify-between items-center border p-2 bg-gray-50 mb-2 rounded-md">
              <span>{d.nombreServicio}</span>
              <button onClick={() => setDetalles(prev => prev.filter(x => x.idServicio !== d.idServicio))} className="text-red-500 text-sm">Quitar</button>
            </li>
          ))}
        </ul>

        <input
          type="number"
          placeholder="Descuento"
          value={descuento}
          className="border p-2 w-full mb-3"
          min={0}
          onChange={(e) => setDescuento(Number(e.target.value))}
        />

        <div className="text-lg font-bold text-green-600 text-right mb-4">
          Total: {formatCurrency(total)}
        </div>

        {error && <p className="text-red-600 text-sm mb-2">{error}</p>}

        <div className="flex justify-end gap-2">
          <button onClick={onClose} className="bg-gray-200 px-4 py-2 rounded-md">Cancelar</button>
          <button onClick={handleSubmit} className="bg-indigo-600 text-white px-4 py-2 rounded-md">Registrar</button>
        </div>
      </div>
    </div>
  );
};

export default VentaRegistroModal;
